.App-logo {
  /* height: 40vmin; */
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body{
  margin: 0;
  padding: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.icon1 {
  display: block;
  width: 41px;
  height: 40px;
  float: left;
  margin: 0 5px 0 0;
  background: url(./rectangle.png) center/contain no-repeat;
}
.icon2 {
  display: block;
  width: 58px;
  height: 40px;
  float: left;
  margin: 0 5px 0 0;
  background: url(./verticalRectangle.png) center/contain no-repeat;
}
.buttonloading{
  width: 148px;
}
.icon3 {
  display: block;
  width: 30px;
  height: 40px;
  float: left;
  margin: 0 5px 0 0;
  background: url(./capture_area.svg) center/contain no-repeat;
}

.description{
  /* color: slategrey; */
  margin: 0.5rem 0px;
  font-size: 0.75rem;
  padding: 1rem 1rem;
  box-shadow: 0px 0px 3px grey;
  /* display: flex; */
  align-items: center;
  justify-content: center;
  border: none;
  width: "7rem";
  /* height: 2rem; */
  /* background: #e02029; */
  cursor: pointer;
  border-radius: 13px;
  /* font-family: "MetropolisRegular"; */
  font-weight: bold;
  color: #fff;
  margin: 0.5rem 5px;
  font-size: 15px;
  padding: 0.2rem 0.2rem;
  font-family: inherit;
  padding: 10px 10px;
  width: 180px;
  color: #708090;
}

.description h2{
  padding: 10px;
